import { useEthers } from "@usedapp/core";
import MM from "../img/metamask.svg"
import WC from "../img/walletconnect.svg"

function Connect() {
  const { account, activateBrowserWallet, deactivate } = useEthers();

  function handleMM() {
    activateBrowserWallet({ type: 'metamask' });
  }

  function handleWC() {
    activateBrowserWallet({ type: 'walletConnectV2' });
  }

  return (
    <>
      {account ? 
       
        <button
          onClick={() => {deactivate()}}
        >
          Disconnect
        </button> 
        :
        <div>
          <button
            style={{height: "30px", weight: "20px", background: "transparent", border: "0", cursor: "pointer"}}
            onClick={() => {handleMM()}}  
          >
            <img 
              style={{height: "20px", weight: "20px"}}            
              src={MM} alt="mm" 
            />
          </button>
          <button
            style={{height: "30px", weight: "20px", background: "transparent", border: "0", cursor: "pointer"}}
            onClick={() => {handleWC()}}
          >
            <img 
              style={{height: "30px", weight: "20px"}}            
              src={WC} alt="WC" 
            />
          </button>
        </div> 
      }
    </>
    
  );
}

export default Connect;
