import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import { config } from './config';
import { ZkSyncTestnet, Goerli, DAppProvider, MetamaskConnector } from "@usedapp/core";
import { WalletConnectV2Connector } from "@usedapp/wallet-connect-v2-connector";

const DAppConfig = {
  readOnlyUrls: {
    [ZkSyncTestnet.chainId]: config.network.ZkSyncTestnet.url,
    [Goerli.chainId]: config.network.Goerli.url,
  },

  connectors: {
    metamask: new MetamaskConnector(),
    walletConnectV2: new WalletConnectV2Connector({
      projectId: "bbbae281079230ddfaa5e02ef36ca6ee",
      chains: [ZkSyncTestnet, Goerli],
      rpcMap: {
        1: config.network.ZkSyncTestnet.url,
        2: config.network.Goerli.url
      },
    })

  },
};

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(

  <DAppProvider config={DAppConfig}>
    <React.StrictMode>
      <App />
    </React.StrictMode>
  </DAppProvider>

  
);