// import { useState } from "react"
import { useEthers, ZkSyncTestnet, Goerli, ConnectorController } from "@usedapp/core";
import { chains } from "../config";

function Network() {
  const { switchNetwork, account, chainId, activateBrowserWallet, library  } = useEthers();

    // const [chain, setChain] = useState(0);

    async function handleSetChain(chainNum) {
        await switchNetwork(Number(chainNum));
        console.log(library.connection.url);
        if (library.connection.url === 'metamask') {
          activateBrowserWallet({ type: 'metamask' });
        } else {
          activateBrowserWallet({ type: 'walletConnectV2' });
        }        
    }

    if(account) {
      return (
        <>
            <div>Current chain: {chains[chainId] ? chains[chainId] : "undefined"}</div>
            <select
                value={chainId} 
                onChange={e => handleSetChain(e.target.value)}
            >
                <option>...</option>
                <option value="5">Goerli</option>
                <option value="280">ZkSyncTestnet</option>
            </select>
        </>
      );
    }
  
}

export default Network;
